//                   Template tablette portrait/mobile paysage: - de 640px/540px
// -----------------------------------------------------------------------------

@media(max-width: 650px) {
  .prezHide,
  .disno650 {
    display: none;
  }

  .jotform-form .label-highlight {
    padding-left: .3rem;
  }
  // slider
  h1.metaTitle {
    line-height: 1.4rem !important;
    font-size: 1.4rem !important;
    margin-bottom: 0 !important;
  }
  .metaAP {
    width: 140px;
  }
  .metaPrice {
    font-size: 2rem !important;
    line-height: 2rem !important;
  }
  a.metaButton {
    font-size: 1.2rem !important;
    width: 80px;
  }

  #banners {
    .banner1 {
      width: 100%;
      float: none;
    }

  }

  #buying{
    h3{
      .aideDevis {
        left: 80%;
      }
    }
  }
  .cards {
    .card {
      width: 49%;
    }
  }

  // tableau commandes ---------------------------------------------------------

  #fbcart_votre{
    table, tbody {
      display: block;
    }
    .kontener{
      display: none;
    }
    td {
      &.lefttd {
        display: block;
        position: absolute;
        float: right;
        right: 30px;
        top: 5px;
        border: 0;
      }
      &.tddesc {
        padding-top: 5px;
        text-align: center;

      }
    }
    tr {
      position: relative;
      width: 100%;
      display: table;
      &:first-of-type{
        display: none;
      }
    }
    th {
       font-size: 0;
       border: none;
       visibility: hidden;
    }
    td, th {
      display: table-row;

    }
  }

  .but_details {
    width: auto;
    min-width:70px;
  }
  .stat, .stat6 {
    background-image: none;
    background: $border;
    &:hover {
      background-image: none;
      background: $border;
    }
  }
  .votre_tab_active,
  .votre_tab_inactive {
    border-bottom: 0;
    font-size: .78rem;
    height: 55px;
  }

  //----------------------------------------------------------------------------
  //                           PAGE PAIEMENTS
  //----------------------------------------------------------------------------

  .paiements_right_con {
    width: 100%;
  }

  .checkbox2 {
    width: 70%;
  }
  #suivant_reg {
    width: 30%;
  }

  //----------------------------------------------------------------------------
  //                                   MODALS
  //----------------------------------------------------------------------------
  #explode{
    .explode{
      display: block;
      width: 49%;

      .accadd {
        .inp_ilosc{
          width: 100%;
        }
        .pilosc {
          display: inline-block;
          width: 100%;
        }
        .prom_sub{
          width: 100%;
          margin-top: 5px;
        }
      }
    }
  }
}

//-------------------------------------------------------------------------540px
@media(max-width: 540px) {
  // invisible -----------------------------------------------------------------
  #preview,
  #preview2,
  .disno540 {
    display: none;
  }

  //----------------------------------------------------------------------------
  .blocAdresse {
    width: 100%;
    margin-right: 0;
    &:nth-of-type(4n){
      margin-right: 0;
    }
    &:nth-of-type(3n){
      margin-right: 0;
    }
    &:nth-of-type(2n){
      margin-right: 0;
    }
  }

  // grille --------------------------------------------------------------------
  #wycena, #estimationTitre, .delivery-date{
    margin-top: 15px;
  }

  #buying,
  #wycena_nag, #estimationTitre,
  #wycena_suma, #estimationPrix
  #dodaj_koszyk {
    @include span(12);
    padding-left: 0;
    padding-right: 0;
  }
  #wycena, #estimation,
  .dateLivraison {
    float: none;
    width: 100%;
    border: none;
  }


  button#responsive-menu-button {
    top: 10px;
    right: 10px;
    margin: 0;
  }




  // menu accs client ---------------------------------------------------------


  //----------------------------------------------------------------------------
  //                           PAGES PRODUITS
  //----------------------------------------------------------------------------

  #buying{
    min-height: 20px;
  }

  // ajustement des icnes dans les pages accueil, akilux, stickers et enseigne

  ul#tarifs, ul#tarifs2 {
    li {
      width: 48%; //120
    }
  }
  .b-it1{
    margin-right: 2%;
  }
  .b-it2 {
    margin-right: 0;
  }
  .b-it3 {
    margin-right: 2%;
  }

  //----------------------------------------------------------------------------
  //                           PAGES ACCES CLIENT
  //----------------------------------------------------------------------------

  // tableau produit command dtail -------------------------------------------

  .disMob0 {
    display: inline;
    font-weight: bold;
    text-transform: uppercase;
    color: $cyan-bloc;
  }

  // tableau devis -------------------------------------------------------------

  #fbcart_cart {
    position: relative;
    float: left;
    display: inline-block;
    @include container(100%);
    width: 100%;
    span {
      &.therest {
        display: none;
      }
    }

    th {
      display: none;
    }

    td {
      display: inline-block;
      width: 100%;
      padding: .5rem;
      background-image: none;

      &.lefttd {
        width: 100%;
      }
      &.noprint{
        width: 5%;
      }
      &.tdtotal{

      }
    }
  }

  #adcart {
    bottom: -6px;
    right: 5px;
  }

  #comment_left{
    padding: 1rem;
  }




}
